export async function onButtonClick(): Promise<void> {
  try {
    await Excel.run(async (context: RequestContext): Promise<void> => {
      /**
       * Insert your Excel code here
       */
      const range: Excel.Range = context.workbook.getSelectedRange();

      // Read the range address
      range.load("address");

      // Update the fill color
      range.format.fill.color = "yellow";

      await context.sync();
      console.log(`The range address was ${range.address}.`);
    });
  } catch (error) {
    console.error(error);
  }
}

function saveAndInsert(mode: "row" | "column"): void {
  Office.context.roamingSettings.set("orientationMode", mode);
  Office.context.roamingSettings.saveAsync();
  //insertFactorials(mode);
}

